<template>
  <div class="header">
    <div class="header_title">
      <span @click="queryAllJobs()">BI任务管理</span>
    </div>

    <!--<div class="header_search">-->
      <!--<el-input v-model="input" class="search" placeholder="输入表名或者描述"></el-input>-->
      <!--<el-button type="primary" class="searchBut" icon="el-icon-search">搜索</el-button>-->
    <!--</div>-->

  </div>

</template>

<script>
  import { queryJobs } from '../api/api.js'

  export default {
        name: "new-header",
        data() {
          return {
            input: ''
          }
        },
      methods: {
        queryAllJobs() {
          queryJobs({}).then((response) => {
            this.$store.state.allJobs = response.data
          })
        },
      }
    }
</script>

<style>
  .header {
    flex: 0 0 50px;
    flex-flow: row nowrap;
    display: flex;
    background-color: #009960;
  }

  .header_title {
    flex: 1;
    text-align: left;
    margin: auto auto auto 20px;
    cursor: pointer;
  }



</style>



