import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const state = {
  updateCount: 0,
  updateJobs: [],
  allJobs: [],
  activeName: 'first',
  loading: false
};

const mutations={
  addUpdateCount(state, record){
    let isHave = false
    for(let i = 0; i < state.updateJobs.length; i++) {
        if(state.updateJobs[i].id == record.id) {
          isHave = true
        }
    }

    if(isHave) {
      return
    }else{
      state.updateCount++;
      state.updateJobs.push(record)
    }
  },
  reduceUpdateCount(state, record){
    state.updateCount--;
    state.updateJobs = state.updateJobs.filter(o => o.id != record.id);
  },

  goDetail(state) {
    state.activeName = 'first';
  }

}

export default new Vuex.Store({
  state,
  mutations
})






