import axios from 'axios'

// let dev_host = 'http://127.0.0.1:8000'

export const queryCategorys = () => {
  return axios.get('/api/categorys')
}

export const queryJobs = (search) => {
  return axios.get('/api/jobs', {params: search})
}

export const queryErrorJobs = () => {
  return axios.get('/api/errorJobs')
}

export const executeJob = (params) => {
  return axios.post('/api/action/execute', params)
}



