<template>
  <div>
  <el-table-column prop="job_name" label="任务名称" ></el-table-column>
  <el-table-column prop="job_doc" label="任务描述"></el-table-column>
  <el-table-column prop="job_table" label="操作表"></el-table-column>


  <el-table-column type="expand" label="详情">
    <template slot-scope="props">
      <el-form label-position="left" inline class="demo-table-expand">
        <el-form-item label="任务路径">
          <span>{{ props.row.job_path }}</span>
        </el-form-item>
        <el-form-item label="任务类别">
          <span>{{ props.row.category_type }}</span>
        </el-form-item>
        <el-form-item label="任务ID">
          <span>{{ props.row.id }}</span>
        </el-form-item>

      </el-form>
    </template>
  </el-table-column>
  </div>
</template>

<script>
    export default {
        name: "new_table"
    }
</script>

<style scoped>

</style>



