<template>
  <el-menu

    class="el-menu-vertical-demo leftNav">

    <el-submenu v-for="(category, index) in job_category" :index="index.toString()" :key="category.code">
      <template slot="title">
        <i class="el-icon-location"></i>
        <span>{{category.category_name}}</span>
      </template>

      <el-menu-item v-for="(nodes, index2) in category.job_nodes" :index="index.toString() + '-' + index2.toString()" :key="nodes.id">
        <router-link @click.native="updateActive()" :to="{name: 'detail', query: {jobNode: nodes.id, jobInfo: nodes, category: category.category_name}}">{{nodes.job_doc}}</router-link>
        <!--{{nodes.job_doc}}-->
      </el-menu-item>

    </el-submenu>

  </el-menu>
</template>


<script>
    import { queryCategorys } from '@/api/api.js'
    export default {
        name: "leftNav",
        data() {
          return {
            job_category: []

          }
        },
        created() {
          queryCategorys({}).then((response) => {
            this.job_category = response.data;
          }).catch((error) => {
            console.log(error);
          })
        },
        methods: {
          updateActive() {
            this.$store.state.activeName = 'first';
          }
        }
    }
</script>

<style>
.leftNav {
  text-align: left;
  height: 100%;
  width: 100%;
}

a {
  color: rgb(64, 158, 255);
  text-decoration: none;
}

.router-link-exact-active {
    color: rgb(64, 158, 255);
}
.router-link-active {
  color: rgb(64, 158, 255);
}
</style>



