<template>
  <el-tabs type="border-card" class="right-content" v-model="$store.state.activeName" @tab-click="handleClick">
    <el-tab-pane name="first">
      <span slot="label"><i class="el-icon-date"></i>
         <router-link @click.native="queryAllJobs()" to="/">任务</router-link>
      </span>
      <router-view/>


    </el-tab-pane>

    <el-tab-pane name="second">
        <span slot="label">
          <el-badge :value="$store.state.updateCount" class="item">
             批量更新
          </el-badge>
        </span>
        <update-record></update-record>
    </el-tab-pane>

    <el-tab-pane name="third">
        <span slot="label">
          <el-badge :value="errorJobsCount" class="item">
             昨日未更新记录
          </el-badge>
        </span>
        <error-job :error-jobs="errorJobs"></error-job>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
  import updateRecord from '@/components/updateRecord';
  import errorJob from '@/components/errorJob';
  import {queryErrorJobs,queryJobs} from '@/api/api.js';
  export default {
    name: 'home',
    data() {
      return {
        errorJobsCount: 0,
        errorJobs: []
      }
    },

    components: {
      updateRecord,
      errorJob
    },

    created() {
      this.queryErrorJobs
    },

    computed: {
      queryErrorJobs() {
        queryErrorJobs({}).then((response) => {
          let jobs = []
          for(let i of response.data) {
            jobs.push(i['error_job'])
          }
          this.errorJobs = jobs;
          this.errorJobsCount = jobs.length;
        }).catch((error) => {
          console.log(error);
        })
      }
    },

    methods : {
      queryAllJobs() {
        this.$store.state.loading = true;
        queryJobs({}).then((response) => {
          this.$store.state.allJobs = response.data;
          this.$store.state.loading = false;
        })
      },

      handleClick(tab) {
        this.$store.state.activeName = tab.name
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-badge {
    position: relative;
    vertical-align: middle;
    display: inline;
  }

  .right-content {
    height: 100%;
  }

</style>



