<template>
  <div>
    <div class="header_search">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-input v-model="inputSearch" class="search" placeholder="输入表名或者描述"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="searchBut" icon="el-icon-search" @click="queryJobsByKey(inputSearch)">搜索</el-button>
        </el-form-item>

      </el-form>

    </div>

  <el-table v-loading="$store.state.loading"
    :data="$store.state.allJobs" border>
    <el-table-column label="操作" width="120">
      <template slot-scope="scope">
        <el-button type="text" @click="opencell(scope.row)" size="small">更新</el-button>
        <el-button type="text" size="mini" icon="el-icon-circle-plus-outline" @click="$store.commit('addUpdateCount', scope.row)">添加</el-button>
      </template>
    </el-table-column>
    <new_table></new_table>
  </el-table>

    <el-dialog
      :title="updateRow.job_table"
      :visible.sync="dialogVisible"
      width="50%"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item>
            <el-input-number :disabled="radio == 2" v-model="updateDays" :min="1" :max="10" label="更新天数">

            </el-input-number>

            <el-radio v-model="radio" label="1">天数</el-radio>
          </el-form-item>

          <el-form-item>
            <el-date-picker :disabled="radio == 1"
                            v-model="startDate"
                            align="right"
                            type="date"
                            format="yyyy 年 MM 月 dd 日"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
            >
            </el-date-picker>
            <el-date-picker :disabled="radio == 1"
                            v-model="endDate"
                            align="right"
                            type="date"
                            format="yyyy 年 MM 月 dd 日"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
            >
            </el-date-picker>

              <el-radio v-model="radio" label="2">日期</el-radio>
          </el-form-item>
        </el-form>


        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateRecord(updateRow)" :disabled="disabled">更 新</el-button>
        </span>

    </el-dialog>
  </div>
</template>



<script>
    import store from '@/vuex/store';
    import new_table from '@/components/table';
    import { queryJobs, executeJob } from '../api/api.js'
    export default {
        name: "allRecord",
        store,
        components: {
          new_table
        },
        created() {
          this.queryAllJobs()
        },

        data() {
          return {
            dialogVisible: false,
            updateDays: 1,
            startDate: '',
            endDate: '',
            radio: '1',
            disabled: false,
            updateRow: {

            },
            form: {
              'name': 'lpj'
            },
            allJobs: [],
            inputSearch: ''
          }
        },
        methods: {

          queryJobsByKey(search) {
            if(search != '') {
              this.$store.state.loading = true;
              queryJobs({search}).then((response) => {
                this.$store.state.allJobs = response.data;
                this.$store.state.isQueryByKey = 1;
                this.$store.state.loading = false;
                this.inputSearch = '';
              })
            }
          },

          queryAllJobs() {
              this.$store.state.loading = true;
              queryJobs({}).then((response) => {
                this.$store.state.allJobs = response.data;
                this.$store.state.loading = false;

              });

          },

          opencell(row) {
            this.dialogVisible = true;
            this.updateRow = row;
          },

          updateRecord(record) {
            this.disabled = true;
            if(this.radio == 1) {
              record['days'] = this.updateDays;
              this.dialogVisible = false;
            }else{
              record['startDate'] = this.startDate;
              record['endDate'] = this.endDate;
            }

              executeJob({
                'record': [record]
              }).then((response) => {
                this.dialogVisible = false;
                this.$message({
                  type: 'success',
                  message: '提交成功'
                });
                this.disabled = false;

              })

            // console.log(record);
          }
        }
    }
</script>

<style>
  .demo-table-expand .el-form-item {
    margin: 0 auto;
    width: 100%;
    overflow: visible;
  }

  .demo-form-inline {
    text-align:center;
  }

  .search {
    margin-right: 10px;
  }


</style>



