<template>

    <div class="updateTable">
      <el-table
        :data="errorJobs" border >
        <new_table></new_table>
      </el-table>

      <div class="updateBtn">
        <el-input-number :disabled="radio == 2" v-model="updateDays" :min="1" :max="10" label="更新天数">

        </el-input-number>
        <!--<el-radio v-model="radio" label="1">天数</el-radio>-->

        <el-button type="primary" @click="updateAllErrorJobs(errorJobs)">更新全部</el-button>
      </div>
    </div>

</template>

<script>
  import {queryErrorJobs, executeJob } from '../api/api.js';
  import new_table from '@/components/table';
  export default {
      name: "errorJob",
      data() {
        return {
          radio: '1',
          updateDays: '1'
        }
      },

      components: {
        new_table
      },
      props: {
        errorJobs: {
          default: [],
          type: Array
        }
      },
      methods: {
        updateAllErrorJobs(allErrorJobs) {
          let newJobs = [];
          for(let i of allErrorJobs) {
            i['days'] = this.updateDays;
            newJobs.push(i);
          }
          console.log(newJobs);
          executeJob({
            'record': newJobs
          }).then((response) => {
            this.$message({
              type: 'success',
              message: '提交成功'
            });
          })
        }
      }

  }
</script>

<style scoped>

</style>



