import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import allRecord from '@/components/allRecord'
import detailJob from '@/components/detailJob'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'home',
    //   component: home
    // },
    {
      path: '/',
      name: 'AllRecord',
      component: allRecord
    },
    {
      path: '/detail',
      name: 'detail',
      component: detailJob
    }
  ]
})



