<template>
  <div class="updateTable">
    <el-table
      :data="$store.state.updateJobs" border >
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" icon="el-icon-delete" size="mini" @click="$store.commit('reduceUpdateCount', scope.row)"></el-button>

        </template>
      </el-table-column>

      <new_table></new_table>
    </el-table>

    <div class="updateBtn">
    <el-input-number :disabled="radio == 2" v-model="updateDays" :min="1" :max="10" label="更新天数">

    </el-input-number>
      <!--<el-radio v-model="radio" label="1">天数</el-radio>-->

    <el-button type="primary" @click="updateAllRecord($store.state.updateJobs)">更新全部</el-button>
    <el-button type="danger" @click="clearAll()">清除全部</el-button>
  </div>
  </div>
</template>

<script>
    import {executeJob } from '../api/api.js';
    import new_table from '@/components/table';
    export default {
        name: "updateRecord",
        components: {
          new_table
        },
        data() {
          return {
            updateDays: 1,
            radio: 1
          }
        },
        methods: {
          updateAllRecord(allRecord) {
            if(this.radio == 1) {
              let newJobs = [];
              for(let i of allRecord) {
                i['days'] = this.updateDays;
                newJobs.push(i);
              }
              executeJob({
                'record': newJobs
              }).then((response) => {
                this.$message({
                  type: 'success',
                  message: '提交成功'
                });
                this.disabled = false;
              })
            }else{
              console.log('')
            }

          },
          clearAll() {
            this.$store.state.updateJobs = []
            this.$store.state.updateCount = 0
          }
        },

    }
</script>

<style>

  .updateTable {
    display: flex;
    flex-direction: column;
  }
  .updateBtn {
    flex: 1;
    margin: 30px auto;
  }



</style>



