<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>任务类别: {{$route.query.category}}</span>
      <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-circle-plus" @click="$store.commit('addUpdateCount', $route.query.jobInfo)">添加</el-button>
    </div>

    <div>
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="任务名称">
          <el-input v-model="$route.query.jobInfo.job_name"></el-input>
        </el-form-item>
        <el-form-item label="任务路径">
          <el-input v-model="$route.query.jobInfo.job_path"></el-input>
        </el-form-item>

        <el-form-item label="任务描述">
          <el-input type="textarea" v-model="$route.query.jobInfo.job_doc"></el-input>
        </el-form-item>

        <el-form-item label="操作表">
          <el-input v-model="$route.query.jobInfo.job_table"></el-input>
        </el-form-item>

        <el-form-item>
          <el-input-number :disabled="radio == 2" v-model="updateDays" :min="1" :max="10" label="更新天数">

          </el-input-number>

          <el-radio v-model="radio" label="1">天数</el-radio>
        </el-form-item>

        <el-form-item >
          <el-date-picker :disabled="radio == 1"
            v-model="startDate"
            align="right"
            type="date"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            >
          </el-date-picker>

          <el-date-picker :disabled="radio == 1"
            v-model="endDate"
            align="right"
            type="date"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            >
          </el-date-picker>

          <el-radio v-model="radio" label="2">日期</el-radio>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="updateRecord($route.query.jobInfo)" :disabled="disabled">更新记录</el-button>
        </el-form-item>
      </el-form>

    </div>

  </el-card>

</template>

<script>
    import {executeJob } from '../api/api.js'
    export default {
      name: "",
      data() {
        return {
          updateDays: 1,
          disabled: false,
          radio: '1',
          startDate: '2018-01-01',
          endDate: '2017-01-02',
          form: {
            name: '',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            resource: '',
            desc: ''
          }
        }
      },
      methods: {
        updateRecord(record) {
          this.disabled = true;
          console.log(this.radio === 1);
          if(this.radio === 1) {
            record['days'] = this.updateDays;
          }else{
            record['startDate'] = this.startDate;
            record['endDate'] = this.endDate;

          }
          executeJob({
            'record': [record]
          }).then((response) => {
            this.$message({
              type: 'success',
              message: '提交成功'
            });
            this.disabled = false;
          })

        }
      }
    }

</script>

<style scoped>


</style>



